<template>
    <div>
        <header class="mui-bar mui-bar-nav">
            <button class="mui-btn mui-btn-blue mui-btn-link mui-btn-nav mui-pull-left" @click="goBack">
                <span class="mui-icon mui-icon-left-nav"></span>返回
            </button>
            <h1 class="mui-title">理赔信息</h1>
        </header>
        <div class="mui-content">
            <div v-if="list==null||list==''" class="policyList wb100 noData">
                <img :src="icon_shape" alt="">
                <span>暂无数据</span>
            </div>
            <div v-else class="policyList policySee wb100">
                <ul class="wb100">
                    <li>
                        <div class="liHeader">
                             <img class="bd" :src="claim_icon" alt>
                             <em>NO.1</em>
                             <span>
                                <img class="arrow" :src="icon_yj" alt="" />
                            </span>
                        </div>
                        <ul class="listText">
                            <li>
                                <span>保单号</span>
                                <span>ZLBX123456789</span>
                            </li>

                            <li>
                                <span>被保险人</span>
                                <span>张三</span>
                            </li>

                            <li>
                                <span>报案时间</span>
                                <span>2018-11-28 14:23:59</span>
                            </li>

                            <li>
                                <span>报案号</span>
                                <span>123456789</span>
                            </li>

                            <li>
                                <span>车牌号</span>
                                <span>鲁B45678</span>
                            </li>
                            
                            <li>
                                <span>案件状态</span>
                                <span>结案</span>
                            </li>

                            <li>
                                <span>核损金额(元)</span>
                                <span>2356.58</span>
                            </li>

                            <li>
                                <span>结案金额(元)</span>
                                <span>2356.58</span>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import claimTitleIcon from '../source/Title_calim@3x.png';
import yj from '../source/icon_yj.png';
import shape from '../source/Shape@3x.png';
import axios from "axios";
export default{
    data() {
        return{
            claim_icon : claimTitleIcon,
            icon_yj : yj,
            policyNo : this.$route.params.policyNo,
            queryListType: this.$route.params.queryListType,
            claimList: [],
            icon_shape: shape
        }
    },
    mounted(){
        this.mui.alert(this.policyNo);
        axios({
            method: 'post',
            url: '/PolicyClaim/claimList',
            params: {
                plyNo : this.policyNo,
                type : this.queryListType
            }
        })
        .then(response => {
           this.claimList = response.data
        })
        .catch(error => {
            this.mui.alert(error);
        })
        
    },
    methods:{
        goBack: function() {
            window.history.go(-1);
        }
    }
}
</script>
<style scoped>
@import '../../../assets/MUI/css/style.css';
</style>


